/* Swiper pagination overrides */
.swiper-pagination-bullet {
  width: 7px;
  height: 7px;
  text-align: center;
  line-height: 20px;
  font-size: 12px;
  color: #000;
  opacity: 1;
  background: rgba(0, 0, 0, 0.2);
}

.swiper-pagination-bullet-active {
  color: #fff;
  background: rgb(31, 37, 41);
}

/* Swiper navigation overrides */
.hero-carousel-container .swiper-button-next:before,
.hero-carousel-container .swiper-button-prev:before {
  content: '' !important;
}

.hero-carousel-container .swiper-button-prev:after {
  content: url('../images/arrow_carousel_left.png');
}

.hero-carousel-container .swiper-button-next:after {
  content: url('../images/arrow_carousel_right.png');
}

.hero-carousel-container .swiper-button-prev,
.hero-carousel-container .swiper-button-next {
  @media (max-width: 845px) and (orientation: landscape) {
    display: none !important;
  }
}

.hero-carousel-container .swiper-button-prev,
.hero-carousel-container .swiper-button-next {
